/* background-color:#2C2E43; */
body{
    background-color: white;
    font-family: "Poppins";
    color: black;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html {
    scroll-behavior: smooth;
  }
ul{
    list-style: none;
}
a{
    text-decoration: none;
    color: white;
}
input{
    outline: none;
    border: none;
}
.logo{
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 2rem;
    color:white;
}
.main{
    width: 100%;
    height: 100vh;
    background: no-repeat;
    background-size: cover;
}
nav{
    padding: 1% 0;
    /* position: relative; */
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    /* left: 0; */
    top: 0;
    width: 100%;
    background-color:black;
    z-index: 100;
}
nav ul{
    display: flex;
}
nav ul li a{
    padding: 0 20px;
    font-size: 1rem;
    text-transform: uppercase;
}
nav ul a:hover{
    color:white;
}
nav .menu-btn,.menu-icon{
    display: none;
}
.info{
    padding-top: 10%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap-reverse;
}
.my-pic{
    width:400px ;
    height: 400px;
    border-radius: 50%;
}
.name{
    font-size: 1.5rem;
    color:black;
    /* top: 20%;
    left: 8%; */
}
.pic{
    padding-left: 20%;
}
#about{
    height: auto;
}
.about{
    min-width: 100%;
    padding: 4% 12%;
}
.card-deck{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.card{
    padding: 30px 10px 20px;
    background: transparent;
    border: none;
    border-radius: none;
}
.card-img{
    width: 300px;
    height: 300px;
    border-radius: 50%;
}
.card-title{
    font-size: 1.5rem;
    color: black;
    font-weight: normal;
}
.h1{
    font-size: 3.5rem;
    text-align: left;
    padding-bottom: 2%;
    color:#B2B1B9;
}
#educate{
    height: auto;
}
.edu{
    padding: 4% 12%;
    display: flex;
    flex-direction: column;
}
.skill{
    padding: 1% 12%;
}
.skill-list{
    display: flex;
    padding: 0% 12%;
}
.icon-box{
    display: flex;
    /* flex-direction: row; */
    align-items: center;
    padding: 20px;
    flex: 1;
    flex-wrap: nowrap;
}
.tech-icon{
    width: 25px;
    height:25px;
    vertical-align: middle;
}
.icon-box h3{
    padding: 0 2%;
}
#contact{
    background-color: black;
    width: 100%;
}
.contact{
    color: white;
    display: flex;
    flex-direction:row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 50px 0;
}
.contact .media{
    padding: 3%;
}
.contact .media a{
    padding: 20px;
}
.contact .media a i{
    font-size:2rem;
    color: white;
}
.connect{
    /* padding: 60px; */
    padding: 3%;
    text-align: center;
}
.connect h3{
    text-align: center;
    padding: 7px 0;
}
.connect p{
    padding: 5px 0;
    font-size: medium;
}
.media h3{
    text-align: center;font-size: 2rem;padding: 30px 0;
}
.connect h3{
    text-align: center;font-size: 2rem;padding: 20px 0;
}
#contact h4{
    text-align: center; color: white;padding: 10px 0;
}
/* Responsive */

@media  screen and (max-width:768px) 
{
    nav{
        display: none;
    }
    .main{
        height: auto;
    }
    .name{
        font-size: 0.8rem;
    }
    .name p{
        font-size: 1.3rem;
    }
    .pic{
        padding-left: 3%;
    }
    .my-pic{
        width:200px ;
        height: 200px;
        border-radius: 50%;
    }
    .card{
        padding: 0;
    }
    .card-img{
        display: none;
    }
    .h1{
        font-size: 1.8rem;
    }
    .card-title{
        font-size: 1rem;
    }
    .edu h2{
        font-size: 1rem;
    }
    .edu p{
        font-size: 0.8rem;
    }
    .tech-icon{
        width: 20px;
        height: 20px;
    }
    .icon-box h3{
        font-size: 0.8rem;
    }
    .skill-list{
        padding: 0 8%;
    }
}